button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  padding: 20px 40px;
  font-size: 18px;
}

button:last-child {
  margin-right: 0;
}

button:hover {
  box-shadow: 0 12px 16px #0000003d, 0 17px 50px #00000030;
}

